
  import Vue from 'vue'

  export default Vue.extend({
    name: 'BMICAL',

   data: () => ({
      weight:0,
      height:0,
      result:0,
    }),
    methods:{
      cal(){
        if(this.weight!=0 && this.height!=0){
            this.result = this.weight/(this.height/100)**2
        }
        else{
            this.result = 0
        }
      }
    },
  })
